<template>
  <div
    class="h-screen flex w-full bg-img vx-row no-gutter items-center justify-center"
    id="page-login"
  >
    <div class="vx-col sm:w-1/2 md:w-1/2 lg:w-3/4 xl:w-3/5 sm:m-0 m-4">
      <vx-card>
        <div slot="no-body" class="full-page-bg-color">
          <div class="vx-row no-gutter justify-center items-center">
            <div class="vx-col hidden lg:block lg:w-1/2">
              <img src="@/assets/images/logo/logo_login.png" alt="login" class="mx-auto" />
            </div>

            <div class="vx-col sm:w-full md:w-full lg:w-1/2 d-theme-dark-bg">
              <div class="p-8 login-tabs-container">
                <div class="vx-card__title mb-4">
                  <h4 class="mb-4">Connexion</h4>
                  <p class="mb-5">Bienvenue à nouveau, veuillez vous connecter.</p>
                </div>

                <div>
                  <vs-input
                    name="email"
                    icon-no-border
                    icon="icon icon-user"
                    icon-pack="feather"
                    label-placeholder="Email"
                    v-model="email"
                    class="w-full mt-4"
                  />

                  <vs-input
                    type="password"
                    name="password"
                    icon-no-border
                    icon="icon icon-lock"
                    icon-pack="feather"
                    label-placeholder="Password"
                    v-model="password"
                    class="w-full mt-6"
                    @keyup.enter="Login"
                  />

                  <div class="flex flex-wrap justify-between my-5">
                    <div class="mb-3"
                      ></div
                    >
                    <router-link to="">Mot de passe oublié?</router-link>
                  </div>
                  <vs-button class="float-right mb-5" @click="Login">Connexion</vs-button>

                  <vs-divider></vs-divider>
                </div>
              </div>
            </div>
          </div>
        </div>
      </vx-card>
    </div>
  </div>
</template>

<script>
import axios from "axios";


export default {
  data() {
    return {
      email: "",
      password: "",
      checkbox_remember_me: false,
      token: "1987dadfb9a99eb8a242b4fe3c6c795e5a0934ee"
    };
  },
  methods: {
    async Login() {
      await axios
        .post("/authentification/login/", {
          email: this.email,
          password: this.password,
        })
        .then((res) => {
          localStorage.setItem("access_token", res.data.key);
          this.$router.push("/");
          this.$vs.notify({
            title: 'Connexion avec succès',
            text: 'vous êtes le bienvenu',
            color: 'success'
          })
        })
        .catch(() => {
          this.$vs.notify({
            title: 'Erreur de conexion',
            text: 'veuillez connecter ultérieurement',
            iconPack: 'feather',
            icon: 'icon-alert-circle',
            color: 'danger'
          })
        });
    },
  },
};

</script>

